import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BankDepositsComponent } from './bank-deposits.component';
import { CreateDepositComponent } from './create-deposit/create-deposit.component';
import { ViewDepositedChequesComponent } from './view-deposited-cheques/view-deposited-cheques.component';

const routes: Routes = [
  { path: 'bank-deposits', component: BankDepositsComponent },
  { path: 'create-deposit', component: CreateDepositComponent },
  { path: 'deposited-cheques/:depositId', component: ViewDepositedChequesComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BankDepositsRoutingModule { }
