import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../services/common.service';
import { HeaderService } from '../../../../../services/header.service';
import { AllocationService } from '../../allocation/allocation/allocation-services/allocation.service';
import { ViewUploadedFileComponent } from '../view-uploaded-file/view-uploaded-file.component';

@Component({
  selector: 'app-view-deposited-cheques',
  templateUrl: './view-deposited-cheques.component.html',
  styleUrls: ['./view-deposited-cheques.component.scss']
})
export class ViewDepositedChequesComponent {
  depositId: number;
  depositedChequeList = [];
  depositDetails;
  fileName;
  //SORTING & PAGINATION
  sort = 1;
  totalRecords: number = 0;
  pageNo: number = 0;
  pagin: number;
  pages: any[] = [];

  constructor(private _activeRoute: ActivatedRoute,
    public _commonService: CommonService,
    public _headerService: HeaderService,
    private _allocationService: AllocationService,
    private _modalService: NgbModal,) { }
  ngOnInit() {
    this._headerService.setTitle('Main Fund Deposits > Deposited Cheque Details View');
    this._activeRoute.params.subscribe(params => {
      this.depositId = params['depositId'];
      this.getChequeDepositDetails();
    })
  }
  getChequeDepositDetails() {
    let params = `?DepositeId=${this.depositId}&Sort=${this.sort}&PageNo=${this.pageNo}&PageSize=6`;
    this._allocationService.getChequeDepositDetails(params).subscribe(response => {
      this.depositedChequeList = response['data'];
      console.log(response);
      this._allocationService.getDepositsById(this.depositId, 'cheque').subscribe(res => {
        console.log(res);
        this.depositDetails = res['data'][0];
        this.fileName = this.depositDetails.filePath.split('/').pop();
        this.totalRecords = res.totalRecordCount;
      })
    })
  }
  viewFile() {
    const modalRef = this._modalService.open(ViewUploadedFileComponent, { size: 'md' });
    modalRef.componentInstance.file = this.depositDetails?.filePath;
  }

  sortByCustomer() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getChequeDepositDetails();
  }
  sortByCheque() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getChequeDepositDetails();
  }
  sortByBank() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getChequeDepositDetails();
  }
  sortByDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getChequeDepositDetails();
  }
  sortByAmount() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getChequeDepositDetails();
  }
  setPage(page: number) {
    this.pageNo = page;
    window.scroll(0, 0);
    this.getChequeDepositDetails();
  }
}
