import { Component, ElementRef, ViewChild } from '@angular/core';
import * as _ from 'underscore';
import { UnsettledSessionService } from '../../cashier/unsettled-session/services/unsettled-session.service';
import { CommonService } from '../../../../services/common.service';
import { SharedService } from '../../../../services/shared.service';
import { AllocationService } from '../allocation/allocation/allocation-services/allocation.service';
import { ViewCashDepositsModalComponent } from './view-cash-deposits-modal/view-cash-deposits-modal.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CollectService } from '../../cashier/collect/services/collect.service';
@Component({
  selector: 'app-bank-deposits',
  templateUrl: './bank-deposits.component.html',
  styleUrls: ['./bank-deposits.component.scss']
})
export class BankDepositsComponent {
  deposits = [];
  banks = [];
  bankAccounts = [];
  sort: number = 1;
  totalRecords: number = 0;
  pageNo = 1;
  pageSize: number = 6;
  pagin: number;
  pages: any[] = [];
  depositId = '';
  amount = '';
  bankAccountId = null;
  bankId = null;
  mainFundAmounts = [];
  isCash = true;
  allowCreate = false;
  cashDepositTypeId = 0;
  @ViewChild('success') success: ElementRef;
  constructor(
    public _unsettleSession: UnsettledSessionService,
    public _commonService: CommonService,
    public _sharedService: SharedService,
    private _allocationService: AllocationService,
    private _collectService: CollectService,
    private _modalService: NgbModal,) {
  }
  ngOnInit() {
    this.getMainFundCash();
    this.getRegisterDetails();
    this.getCashDeposits();
    this.getBanks();
    this.getUserPermission();
    this.getBankAccounts();
  }
  getUserPermission() {
    let permssions = JSON.parse(localStorage.getItem('permissions'));
    this.allowCreate = permssions.find(x => x.permissionCatId === 26).permissionSubCategories.find(y => y.permissionSubCatId === 66).permissionItems.find(z => z.permissionItemId === 147).selected;
  }
  getCashDeposits() {
    let filters = `?bankId=${this.bankId && this.bankId || ''}&AccountId=${this.bankAccountId && this.bankAccountId || ''}
    &DepositeId=${this.depositId}&DepositeType=${this.isCash && 'cash' || 'cheque'}&amount=${this.amount}&Sort=${this.sort}&PageNo=${this.pageNo - 1}&pageSize=${this.pageSize}`
    this._unsettleSession.getCashDeposits(filters).subscribe((response: any) => {
      this.deposits = response['data'];
      this.totalRecords = response?.totalRecordCount;
    })
  }
  getRegisterDetails() {
    return this._collectService
      .GetRegisterDetails(this._sharedService.getRegister.registerObj?.id).subscribe((response: any) => {
        if (response) {
          this.cashDepositTypeId = response?.['data']['cashDepositTypeId'];
        }
      });
  }

  getCashDepositDetails(id, type) {
    if (type == 'cash') {
      this._allocationService.getCashDepositDetails(id).subscribe(response => {
        console.log(response);
        const modalRef = this._modalService.open(ViewCashDepositsModalComponent, { size: 'lg' });
        modalRef.componentInstance.depositDetails = response['data'][0];
      })
    }
    else {
      this._commonService.NavigateToRoute('/main-fund/deposited-cheques/' + id)
    }
  }
  depositType(event) {
    this.isCash = event.target.value == 'cash' ? true : false;
    this.pageNo = 1;
    this.sort = 1;
    this.getCashDeposits();
  }
  clearAmountInput() {
    this.amount = '';
    this.getCashDeposits();
  }
  clearDepositInput() {
    this.depositId = '';
    this.getCashDeposits();
  }
  onDepositChange() {
    this.getCashDeposits();
  }
  onAmountChange() {
    this.getCashDeposits();
  }
  getBanks() {
    this._unsettleSession.getBanks().subscribe(response => {
      this.banks = response['data'];
    })
  }
  onBankChange(event) {
    this.bankAccounts = [];
    this.bankAccountId = null;
    this.getBankAccounts(event?.bankId);
    this.getCashDeposits();
  }
  getBankAccounts(bankId?) {
    //let param = `?bankId=${id}&status=2001&pageNo=0&pageSize=6`
    let param = `?status=2001&pageNo=0&pageSize=1000`
    if (bankId) {
      param += `&bankId=${bankId}`
    }
    this._unsettleSession.getBankAccounts(param).subscribe(response => {
      this.bankAccounts = response['data'];
      console.log(this.bankAccounts);
    })
  }
  onBankAccountChange(event) {
    this.getCashDeposits();
  }
  getMainFundCash() {
    this._allocationService.getMainFundCash().subscribe(response => {
      console.log(response['data']);
      //this.mainFundAmounts = response?.data;
      let data = response?.data;
      data.forEach(item => {
        if (!this.mainFundAmounts.some(x => x.accountTypeID == item?.accountTypeID)) {
          this.mainFundAmounts.push(item);
        }
      })
    })
  }
  // Sorting Functions
  sortByDepositId() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getCashDeposits()
  }
  sortByBankName() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getCashDeposits()
  }
  sortByAccountName() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getCashDeposits()
  }
  sortByAccountNo() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getCashDeposits()
  }
  sortByAmount() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getCashDeposits()
  }
  /**
   * pagination
   * @param page page no
   */
  pageChange(page: number) {
    this.pageNo = page;
    window.scroll(0, 0);
    this.getCashDeposits()
  }

}
